interface MarketElement extends Element {
  componentOnReady: () => Promise<unknown>;
}

function isMarketElement(node: Node): node is MarketElement {
  return (
    (node as MarketElement)?.tagName?.includes('MARKET-') &&
    typeof (node as MarketElement).componentOnReady === 'function'
  );
}

export async function waitForMarketHydration(rootElement: Element) {
  const promises: Promise<unknown>[] = [];
  const waitForDidLoad = (promises: Promise<unknown>[], el: Document | Element) => {
    if (el !== null && el.nodeType === 1) {
      for (let i = 0; i < el.children.length; i++) {
        const childElm = el.children[i];
        if (isMarketElement(childElm)) {
          promises.push(childElm.componentOnReady());
        }
        waitForDidLoad(promises, childElm);
      }
    }
  };
  waitForDidLoad(promises, rootElement);
  await Promise.all(promises).catch((e) => console.error(e)); // eslint-disable-line no-console
}
